<template>
    <div class="zp-box">
        <zonep_form v-if="$store.state.show_zonepform"></zonep_form>
        <act_pay v-if="$store.state.show_actpay"></act_pay>

        <div class="zp-top">
            <div class="zp-wrp">
                <!--  -->
                <div class="zp-info">
                    <p class="zp-info-content">
                        <b class="zp-info-content-one">
                            A
                        </b>
                        lpha Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader,Zone is a professoional room invested by Alphatrader
                    </p>
                </div>
                <!-- apply -->
                <ul class="zp-model">
                    <li class="zp-model-item" v-for="item in zonePlus_list">
                        <!--  -->
                        <h3 class="zp-model-item-title"> {{item.name}}-trading Model</h3>
                        <!-- -->
                        <p class="zp-model-item-intro"></p>
                        <!-- icon -->
                        <p class="zp-model-item-icon">
                            <span class="zp-model-item-icon-img"></span>
                        </p>
                        <!-- price -->
                        <p class="zp-model-item-sel">
                            <span class="zp-model-item-left"> 0.01/</span>
                            <span class="zp-model-item-right">  0.02/</span>
                        </p>
                        <!--  -->
                        <div class="zp-model-item-content">
                            <p>Alpha Zone18</p>
                            <p>Alpah TV</p>
                            <p>Alpah Zone</p>
                            <p><span>[]</span></p>
                            <p><span>[]</span></p>
                        </div>
                        <!-- apply -->
                        <p class="zp-model-item-option">
                            <button class="zp-model-item-apply" @click="pay(item.id,item.location)"> Apply </button>
                        </p>
                        <!--  -->
                        <p class="zp-model-item-des">
                            
                        </p>
                    </li>
                </ul>
            </div>
        </div>
        <!-- footer -->
        <div class="zp-bottom">
            <div class="zp-bottom-mask">
                <button class="zp-bottom-apply"> Apply Now </button>
            </div>
        </div>
    </div>
</template>

<script>
    import act_pay from './Act_pay'
    import zonep_form from './Zonep_form'
    export default {
        data() {
            return {
                zonePlus_list: []
            }
        },
        mounted(){
            const self = this
            let formData = new FormData()
            fetch(self.$store.state.api_addr + 'ZoneAndPlus/zonePlus_list',{
                method: 'post',
                mode: 'cors',
                body: formData
            }).then((res) => {
                res.ok && res.json().then((json) => {
                    if(json.archive.status === 0 ) {
                        self.zonePlus_list= json.data
                    }
                })
            })
        },
        methods: {
            pay(id,location) {
                const self = this
                self.$store.dispatch('CHANGEZONEPLUSINFO',location)
                if(!sessionStorage.getItem('token')){
                    self.$store.dispatch('TOGGLELOGIN','on')
                }else{
                    self.$store.dispatch('TOGGLEZONEPFORM','on')
                    self.$store.dispatch('CHANGEZONEPID',id)
                }
            }
        },
        components: {
            zonep_form,
            act_pay
        }
    }
</script>

<style scoped lang="scss">
    @import '../css/alpha.scss';
    .zp-box{
        width: 100%;
        overflow: hidden;
        .zp-top{
            background: url(../assets/images/act_banner.jpg) top center no-repeat;
            background-size: 100%;
            overflow: hidden;
            min-height: 1000px;
            margin-bottom: 60px;
            .zp-wrp{
                width: 1140px;
                margin:150px auto 60px;
                overflow: hidden;
                // 
                .zp-info{
                    width: 65%;
                    background: rgba(51,61,76,.7);
                    color: #fff;
                    padding: 5px;
                    margin-bottom: 50px;
                    .zp-info-content{
                        padding: 20px 30px 0 60px;
                        font-size: 14px;
                        line-height: 2 ;
                        box-sizing: border-box;
                        .zp-info-content-one{
                            font-size: 40px;
                            font-weight: 400;
                            line-height: 20px;
                        }
                    }
                }
                // apply
                .zp-model{
                    width: 100%;
                    padding: 0;
                    margin: 0;
                    background: transparent;
                    font-size: 14px;
                    .zp-model-item{
                        padding: 50px 70px;
                        text-align: center;
                        border: 1px solid #f3f6f8;
                        width: 550px;
                        min-height: 300px;
                        background: rgba(255,255,255,.98);
                        float: left;
                        box-sizing: border-box;
                        &:first-child{
                            margin-right: 40px;
                        }
                        &:hover{
                            box-shadow: 0px 50px 50px 0 rgba(0,0,0,.05);
                        }
                        // 
                        .zp-model-item-title{
                            color: $primary;
                            font-size: 20px;
                            font-weight: bold;
                            background: url(../assets/images/zplus_arrow.png) bottom center no-repeat;
                            background-size: 18%;
                            margin: 0;
                            padding-bottom: 20px;
                        }
                        // 
                        .zp-model-item-intro{
                            margin: 0;
                            padding: 15px 0;
                            font-size: 14px;
                            color:$gray2;
                        }
                        // icon
                        .zp-model-item-icon{
                            margin: 0 auto;
                            padding: 0;
                            width: 100px;
                            height: 100px;
                            .zp-model-item-icon-img{
                                background-image: url(../assets/images/prop_icon.png);
                                background-size: 100%;
                                width: 100%;
                                height: 100%;
                                display: block;
                            }
                        }
                        // price
                        .zp-model-item-sel{
                            .zp-model-item-left,.zp-model-item-right{
                                color: #333;
                            }
                            .zp-model-item-left{
                                line-height: 30px;
                            }
                            .zp-model-item-right{
                                /*position: relative;
                                top: 10px;*/
                            }
                        }
                        // 
                        .zp-model-item-content{
                            margin: 0;
                            padding: 0;
                            color: $gray3;
                            padding-bottom: 15px;
                            span{
                                color: $danger;
                            }
                        }
                        // apply
                        .zp-model-item-option{
                            margin: 0;
                            padding: 0;
                            .zp-model-item-apply{
                                width: 100px;
                                height: 32px;
                                font-size: 16px;    
                                border-radius: 1px;
                                outline: none;
                                cursor: pointer;
                                color: $primary;
                                background: transparent;
                                border: 1px solid $primary;
                                transition: all .2s;
                                &:hover{
                                    color: #fff;
                                    background: $primary;    
                                }
                            }
                        }
                        // 
                        .zp-model-item-des{
                            margin: 0;
                            padding: 0;
                            font-size: 16px;   
                            color: $gray2;
                            padding: 15px 0;
                            line-height: 1.5;
                        }
                    }
                    .zp-model-item:last-child{
                        .zp-model-item-icon-img{
                            background-image: url(../assets/images/algo_icon.png);
                            background-size: 100%;
                            width: 100%;
                            height: 100%;
                            display: block;
                        }
                    }
                }
            }
        }
        // footer
        .zp-bottom{
            width: 100%;
            height: 200px;
            overflow: hidden;
            background: url(../assets/images/zone_bottom.png) center center no-repeat;
            background-size: 100% 100%;
            .zp-bottom-mask{
                padding-top: 80px;
                .zp-bottom-apply{
                    display: block;
                    margin: 0 auto;
                    width: 200px;
                    height: 40px;
                    border: 1px solid #fff;
                    border-radius: 2px;
                    background: transparent;
                    color: #fff;
                    outline: none;
                    // cursor: pointer;
                }
            }
        }
    }
</style>


